import React from 'react'
import { NavLink } from 'react-router-dom'
import styled from 'styled-components'

const Nav: React.FC = () => {
    return (
        <StyledNav>
            <StyledNav1>
            <StyledLink exact activeClassName="active" to="/home">
                    Home
           </StyledLink>
            <StyledLink exact activeClassName="active" to="/farms">
                Dungeons
           </StyledLink>
            <StyledLinkurl target="_blank" href="https://guardfinance.medium.com/forestguard-1a1a4d8e0a">
                Tokenomics
      </StyledLinkurl>

            </StyledNav1>

            <StyledNav2>
                <StyledLinkurl target="_blank" href="https://app.uniswap.org/#/swap?outputCurrency=0xf7Eb799E402F3cAC0e042928853d516775E66F8e">
                    UniSwap
      </StyledLinkurl>
            <StyledLinkurl target="_blank" href="https://forestguard.github.io/swap/">
                ForestSwap
      </StyledLinkurl>
            <StyledLinkurl target="_blank" href="http://forestguard.github.io/sight">
                ForestSightVision
      </StyledLinkurl>
            </StyledNav2>
        </StyledNav>
    )
}

const StyledNav1 = styled.nav`
  align-items: center;
  padding-left: 30px;
  display: block;
  margin-left: -30px;
`


const StyledNav2 = styled.nav`
  padding-top: 20px;
  align-items: center;
  display: block;
  margin-left: -30px;
`

const StyledNav = styled.nav`
  margin-left:-50px;
  align-items: left;
  display: block;
`
const StyledLinkurl = styled.a`
  font-family:  sans-serif;
  color: ${(props) => props.theme.color.grey[400]};
  padding-left: ${(props) => props.theme.spacing[3]}px;
  padding-right: ${(props) => props.theme.spacing[3]}px;
  text-decoration: none;
  &:hover {
    color: ${(props) => props.theme.color.grey[500]};
  }
`
const StyledLink = styled(NavLink)`
  font-family:  sans-serif;
  color: ${(props) => props.theme.color.grey[400]};
  font-weight: 700;
  padding-left: ${(props) => props.theme.spacing[3]}px;
  padding-right: ${(props) => props.theme.spacing[3]}px;
  text-decoration: none;
  &:hover {
    color: ${(props) => props.theme.color.grey[500]};
  }
  &.active {
    color: ${(props) => props.theme.color.primary.main};
  }
  @media (max-width: 400px) {
    padding-left: ${(props) => props.theme.spacing[2]}px;
    padding-right: ${(props) => props.theme.spacing[2]}px;
  }
`

const StyledAbsoluteLink = styled.a`
  color: ${(props) => props.theme.color.grey[400]};
  font-weight: 700;
  padding-left: ${(props) => props.theme.spacing[3]}px;
  padding-right: ${(props) => props.theme.spacing[3]}px;
  text-decoration: none;
  &:hover {
    color: ${(props) => props.theme.color.grey[500]};
  }
  &.active {
    color: ${(props) => props.theme.color.primary.main};
  }
  @media (max-width: 400px) {
    padding-left: ${(props) => props.theme.spacing[2]}px;
    padding-right: ${(props) => props.theme.spacing[2]}px;
  }
`

export default Nav
