import BigNumber from 'bignumber.js'
import { ethers } from 'ethers'
import { supportedPools } from './lib/constants'
import axios from 'axios';

BigNumber.config({
    EXPONENTIAL_AT: 1000,
    DECIMAL_PLACES: 80,
})

const GAS_LIMIT = {
    STAKING: {
        DEFAULT: 200000,
        SNX: 850000,
    },
}

export const getMasterChefAddress = (sushi) => {
    return sushi && sushi.masterChefAddress
}
export const getSushiAddress = (sushi) => {
    return sushi && sushi.sushiAddress
}
export const getWethContract = (sushi) => {
    return sushi && sushi.contracts && sushi.contracts.weth
}

export const getMasterChefContract = (sushi) => {
    return sushi && sushi.contracts && sushi.contracts.masterChef
}
export const getSushiContract = (sushi) => {
    return sushi && sushi.contracts && sushi.contracts.sushi
}

export const getFarms = (sushi) => {
    return sushi
        ? sushi.contracts.pools.map(
            ({
                pid,
                name,
                symbol,
                icon,
                tokenAddress,
                tokenSymbol,
                tokenContract,
                lpAddress,
                lpContract,
            }) => ({
                pid,
                id: symbol,
                name,
                lpToken: symbol,
                lpTokenAddress: lpAddress,
                lpContract,
                tokenAddress,
                tokenSymbol,
                tokenContract,
                earnToken: 'Forest',
                earnTokenAddress: sushi.contracts.sushi.options.address,
                icon,
            }),
        )
        : []
}

export const getPoolWeight = async (masterChefContract, pid) => {
    const { allocPoint } = await masterChefContract.methods.poolInfo(pid).call()
    const totalAllocPoint = await masterChefContract.methods
        .totalAllocPoint()
        .call()
    return new BigNumber(allocPoint).div(new BigNumber(totalAllocPoint))
}

export const getEarned = async (masterChefContract, pid, account) => {
    return masterChefContract.methods.pendingForest(pid, account).call()
}

export const getMonsterHP = async (masterChefContract, pid) => {
    return masterChefContract.methods.getMonsterHP(pid).call()
}

export const getBlockReward = async (masterChefContract) => {
    return masterChefContract.methods.TreasurePerBlock().call()
}

export const getTotalLPWethValue = async (
    masterChefContract,
    wethContract,
    lpContract,
    tokenContract,
    pid,
) => {
    if (!supportedPools[pid].stakeOnly) {
        // Get balance of the token address
        const tokenAmountWholeLP = await tokenContract.methods
            .balanceOf(masterChefContract.options.address)
            .call()
        const tokenDecimals = await tokenContract.methods.decimals().call()
        const lpDecimals = await lpContract.methods.decimals().call()
        // Get the share of lpContract that masterChefContract owns
        const balance = await lpContract.methods
            .balanceOf(masterChefContract.options.address)
            .call()

        // Convert that into the portion of total lpContract = p1
        let totalSupply = await lpContract.methods.totalSupply().call()

        let balanceOfForestTressure = await tokenContract.methods
            .balanceOf(lpContract.options.address)
            .call()

        // Get total weth value for the lpContract = w1
        let lpContractWeth = await wethContract.methods
            .balanceOf(lpContract.options.address)
            .call()

        // Return p1 * w1 * 2
        const portionLp = new BigNumber(balance).div(new BigNumber(totalSupply))
        const lpWethWorth = new BigNumber(lpContractWeth)
        const totalLpWethValue = portionLp.times(lpWethWorth).times(new BigNumber(2))
        // Calculate
        const tokenAmount = new BigNumber(tokenAmountWholeLP)
            .times(portionLp)
            .div(new BigNumber(10).pow(tokenDecimals))

        const wethAmount = new BigNumber(lpContractWeth)
            .times(portionLp)
            .div(new BigNumber(10).pow(18))

        balanceOfForestTressure = balanceOfForestTressure / Math.pow(10, tokenDecimals);
        let totalWETH = lpContractWeth;
        lpContractWeth = lpContractWeth / Math.pow(10, lpDecimals);
        lpContractWeth = lpContractWeth / balanceOfForestTressure;
        console.log('totalSupply ' + balanceOfForestTressure);
        console.log('lpContractWeth ' + lpContractWeth );
        return {
            tokenAmount,
            wethAmount,
            totalWethValue: totalWETH,
            tokenPriceInWeth: new BigNumber(lpContractWeth).times(new BigNumber(10).pow(lpDecimals)),
            poolWeight: await getPoolWeight(masterChefContract, pid),
        }
    }
    else {
        let balance = await tokenContract.methods
            .balanceOf(masterChefContract.options.address)
            .call()

        //let totalSupply = await tokenContract.methods.totalSupply().call()

        const tokenDecimals = await tokenContract.methods.decimals().call()

        balance = balance / Math.pow(10, tokenDecimals);
        //totalSupply = totalSupply / Math.pow(10, tokenDecimals);

        const tokenAmount = 0;
        const wethAmount = 0;

        //https://api.coingecko.com/api/v3/simple/token_price/ethereum?contract_addresses=0xdac17f958d2ee523a2206206994597c13d831ec7&vs_currencies=eth

        const addr = supportedPools[pid].tokenAddresses[1].toLowerCase();
        const request = 'https://api.coingecko.com/api/v3/simple/token_price/ethereum?contract_addresses=' + addr + '&vs_currencies=eth';
        //console.log('json ' + JSON.stringify(requestResult));
        // let loop = true; 
        let ethval = 0;
        let requestResult = '';
        const poolWeight = await getPoolWeight(masterChefContract, pid);
        try {
             requestResult = await axios.get(request)
                .catch(function (error) {
                    if (error.response) {
                        // Request made and server responded
                        console.log('error response');
                        console.log(error.response.data);
                        console.log(error.response.status);
                        console.log(error.response.headers);
                    } else if (error.request) {
                        // The request was made but no response was received
                        console.log('error request ' + error.request);
                    } else {
                        // Something happened in setting up the request that triggered an Error
                        console.log('Error message ', error.message);
                    }
                });

            ethval = requestResult.data[addr]['eth'];
            console.log('addr ' + addr + ' ethval ' + ethval + ' poolWeight ' + poolWeight + ' balance ' + balance);
        }
        catch (e) {
            console.log('failed ' + addr + ' result ' + JSON.stringify(requestResult));
        }

        const totalWethValue = (balance) * ethval * Math.pow(10, 18); // this need to be set for stake only
        const tokenPriceInWeth = 0;

        return {
            tokenAmount,
            wethAmount,
            totalWethValue,
            tokenPriceInWeth,
            poolWeight
        }
    }
}

export const approve = async (lpContract, masterChefContract, account) => {
    return lpContract.methods
        .approve(masterChefContract.options.address, ethers.constants.MaxUint256)
        .send({ from: account })
}

export const getSushiSupply = async (sushi) => {
    return new BigNumber(await sushi.contracts.sushi.methods.totalSupply().call())
}

export const stake = async (masterChefContract, pid, amount, account) => {
    try {
        return masterChefContract.methods
            .deposit(
                pid,
                new BigNumber(amount).times(new BigNumber(10).pow(18)).toString(),
            )
            .send({ from: account })
            .on('transactionHash', (tx) => {
                console.log(tx)
                return tx.transactionHash
            })
    }
    catch (ex) {
        console.log(ex);
    }
}

export const unstake = async (masterChefContract, pid, amount, account) => {
    try {
        return masterChefContract.methods
            .withdraw(
                pid,
                new BigNumber(amount).times(new BigNumber(10).pow(18)).toString(),
            )
            .send({ from: account })
            .on('transactionHash', (tx) => {
                console.log(tx)
                return tx.transactionHash
            })
    }
    catch (ex) {
        console.log(ex);
    }
}
export const harvest = async (masterChefContract, pid, account) => {
    return masterChefContract.methods
        .deposit(pid, '0')
        .send({ from: account })
        .on('transactionHash', (tx) => {
            console.log(tx)
            return tx.transactionHash
        })
}

export const getStaked = async (masterChefContract, pid, account) => {
    try {
        const { amount } = await masterChefContract.methods
            .userInfo(pid, account)
            .call()
        return new BigNumber(amount)
    } catch {
        return new BigNumber(0)
    }
}

export const redeem = async (masterChefContract, account) => {
    let now = new Date().getTime() / 1000
    if (now >= 1597172400) {
        return masterChefContract.methods
            .exit()
            .send({ from: account })
            .on('transactionHash', (tx) => {
                console.log(tx)
                return tx.transactionHash
            })
    } else {
        alert('pool not active')
    }
}
