import React from 'react'
import coin from '../../assets/img/goldcoin.png'

interface SushiIconProps {
    size?: number
    v1?: boolean
    v2?: boolean
    v3?: boolean
}

const ForestIcon: React.FC<SushiIconProps> = ({ size = 36, v1, v2, v3 }) => (
    <span
        role="img"
        style={{
            fontSize: size,
            filter: v1 ? 'saturate(0.5)' : undefined,
        }}
    >
        <img src={coin} height="60" />
    </span>
)

export default ForestIcon
