import React from 'react'
import chest from '../../assets/img/chest.png'

interface ChestIconProps {
    size?: number
    v1?: boolean
    v2?: boolean
    v3?: boolean
}

const ChestIcon: React.FC<ChestIconProps> = ({ size = 36, v1, v2, v3 }) => (
    <span
        role="img"
        style={{
            fontSize: size,
            filter: v1 ? 'saturate(0.5)' : undefined,
        }}
    >
        <img src={chest} height="60" style={{ flexDirection: 'row', justifyContent: 'flex-end' }}/>
    </span>
)

export default ChestIcon
